import { getApiEnvironmentConfig } from "@rp/common/utils"
import { getClient } from "@rp/react/axios-api"
import {
  requestErrorInterceptor,
  requestInterceptor,
  responseErrorInterceptor,
  responseInterceptor,
} from "./interceptors"

const envConfig = getApiEnvironmentConfig()
const url = envConfig.url

if (url === undefined) {
  // We must have a URL defined, so force the app to crash
  throw new Error("Undefined API URL")
}

const options = {
  headers: {
    "X-Client-Version": "1.0.0",
  },
}

const interceptors = {
  request: { success: requestInterceptor, error: requestErrorInterceptor },
  response: { success: responseInterceptor, error: responseErrorInterceptor },
}

const axiosClient = getClient(url, options, interceptors)

export { axiosClient }
